body{
	width: 100%;
	height: 100vh;
	font-size: 32px;
	font-family: monospace;
	color: #111;
	overflow: hidden;
}

#app{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
}

.favourite{
	background: #002;
	width: 30%;
	height: 100%;
}
.main-card{
	width: 70%;
	height: 100%;
}


.copy-color-input{
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
	opacity: 0;
}


@media (max-width: 960px){
	#app{
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-ms-flex-direction: column;
		flex-direction: column;
	}

	.favourite{
		width: 100%;
		height: 35%;
	}
	.main-card{
		width: 100%;
		height: 65%;
	}
}
