.favourite{
	color: #fff;
	font-size: 30px;
	border-right: 1px solid #002;
	overflow-y: auto;
}

.favourite::-webkit-scrollbar{
	width: 6px;
	background: #fff;
}
.favourite::-webkit-scrollbar-track{

}
.favourite::-webkit-scrollbar-thumb{
	background: #002;
}


.favourite p{
	padding: 15px;
	padding-left: 20px;
	border-bottom: 1px solid #888
}
.favourite p i{
	color: #FF6570;
} 


.favourite-cards{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}
.favourite-card{
	position: relative;
	width: 100%;
	height: 220px;
	overflow: hidden;
}

.favourite-card .card{
	height: 100%;
	font-size: 30px;
}
.favourite-card .card-controls{
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	height: auto;
	-webkit-transform: translateY(100%);
	transform: translateY(100%);
	opacity: 0;
	-webkit-transition: .4s;
	transition: .4s;
}
.favourite-card:hover .card-controls{
	-webkit-transform: translateY(0);
	transform: translateY(0);
	opacity: 1;
}



@media (min-width: 1400px){
	.favourite-card{
		height: 240px;
	}
}

@media (min-width: 2000px ){
	.favourite-card{
		width: 50%;
		height: 200px;
	}
}

@media (min-width: 3000px){
	.favourite-card{
		width: 33.33%;
	}
}


@media (max-width: 1166px){
	.favourite-card{
		height: 180px;
	}
}

@media( max-width: 960px){
	.favourite{
		border: none;
		border-bottom: 1px solid #002;
	}

	.favourite-card{
		width: 33.33%;
		height: 200px;
	}
}

@media (max-width: 820px){
	.favourite-card{
		width: 50%;
	}
}

@media (max-width: 640px){
	.favourite-card{
		height: 180px;
	}
}

@media (max-width: 560px){
	.favourite-card{
		width: 100%;
		height: 200px;
	}
}

@media (max-width: 440px){
	.favourite-card{
		height: 190px;
	}
}

@media (max-height: 680px){
	.favourite-card{
		height: 165px;
	}
}
