.main-card{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
}
.card{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  font-size: 40px;
  color: #fff;
  text-shadow: 0px 0px 10px#000a;
}


.card-controls{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	height: auto;
	padding: 6px;
	background: #fff;
}

.card-controls > span{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	text-align: center;
	width: 1.6em;
	height: 1.6em;
	margin: 0 12px;

	border: 2px solid #0004;
	border-radius: 12px;
	-webkit-transition: .3s;
	transition: .3s;
	cursor: pointer;
}
.card-controls > span:hover{
	-webkit-transform: scale(1.2);
	transform: scale(1.2);
}


.change-color-link{
	width: 1.9em!important;
	height: 1.9em!important;
}

.favourite-link{
	color: #FF6570;
}
.change-color-link, .copy-link{
	color: #217FB2;
}



.copied{
	width: 5.5em!important;
}
.copied i{
	display: none;
}